.about {
    grid-area: about;
  }
  
  .habilities {
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: auto auto auto;
    grid-gap: 1em;
    margin-top: 2em;
  }
  
  .hability > span {
    display: block;
    border-radius: 5px;
    padding: 0.5em 1em;
    font-weight: 600;
    background: rgba(5, 111, 255, 0.1);
  }
  
  .subHabilities {
    padding: 20px 5px 10px 0;
  }
  
  .subHabilities li {
    display: flex;
    align-items: center;
    padding: 0.5em;
  }
  
  .subHabilities span {
    padding-left: 1em;
  }
  
  .subHabilities img {
    height: 30px;
    width: 50px;
  }
  
  @media screen and (min-width: 1024px) {
    .habilities {
      grid-template-columns: auto auto auto;
      grid-auto-rows: 1fr;
      grid-gap: 1em;
    }
  }
  